@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{height:100vh;width:100vw;background-color:#338dc1}#root{height:100%;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}#root h1{font-family:"Pacifico", cursive !important;font-size:2.2em;color:#fff;z-index:2;opacity:0.99}

.todo-container{padding:20px;margin:10px;width:60%;height:80%;border-radius:20px;background:#338dc1;box-shadow:20px 20px 60px #3082b1, -20px -20px 60px #42b0ef;display:flex;flex-direction:column;justify-content:center;align-items:center}.todo-list{width:100%;height:100%;overflow-y:scroll;padding:30px}.todo{padding:12px 10px;color:rgba(255,255,255,0.925);width:100%;height:50px;display:flex;justify-content:space-between;align-items:center;transition:all 400ms ease-in-out;border-radius:10px;background:#338dc1;box-shadow:5px 5px 10px #3082b1, -0px -0px 10px #42b0ef;margin-bottom:10px}.todo p{margin-right:40px;font-family:"Open Sans";text-align:left}.todo:hover{transform:scale(1.05);transition:all 200ms ease-out;color:white}.todo button{margin:0px 10px;border:none;padding:5px 10px;font-size:0.8em;border-radius:5px}.todo .delete{background-color:#ff3b3b;color:white}.todo-list::-webkit-scrollbar{width:5px}.todo-list::-webkit-scrollbar-track{background-color:transparent;border-radius:4px}.todo-list::-webkit-scrollbar-thumb{background-color:#fff;border-radius:4px;outline:1px solid #000}form{width:100%;padding:0px 30px}#input-field{padding:5px 10px;border-radius:1px;border:none;border-radius:5px;width:100%;font-family:'Poppins';height:40px;background-color:black;color:#fff}

